import {observer} from 'mobx-react'
import React from 'react'
import ToDoItems from './ToDoItems';
import ToDoStore from '../store/ToDoStore';
import ToDoFilter from '../store/ToDoFilter'

@observer
export default class Footer extends React.Component {
    render() {
        let leftItems = ToDoStore.todos.filter((todo) => todo.completed === false)
        return (
            <footer className="footer">
                <ul className="filters">
                    <li className="todo-count"><a><strong>{leftItems.length} Item(s) left</strong></a></li>
                    <li><a className={ToDoFilter.flag === "all" ? "active" : ""} onClick={() => ToDoFilter.allFilter(ToDoStore.todos)} href="#">All</a></li>
                    <li><a className={ToDoFilter.flag === "active" ? "active" : ""} onClick={() => ToDoFilter.activeFilter(ToDoStore.todos)} href="#">Active</a></li>
                    <li><a className={ToDoFilter.flag === "com" ? "active" : ""} onClick={() => ToDoFilter.comFilter(ToDoStore.todos)} href="#">Completed</a></li>
                    <li className="clear-completed"><a href="#" onClick={ToDoFilter.deleteAllComp}>Clear Completed</a></li>
                </ul>
                <p className="info">Published By Ali</p>
          </footer>
        );
    }
}


