import React from 'react';
import {observer} from 'mobx-react'
import ToDoStore from '../store/ToDoStore'
import ToDoFilter from '../store/ToDoFilter'

@observer
export default class ToDoItem extends React.Component {

    onToggle = () => {
        this.props.todo.completed = !this.props.todo.completed;
    }

    render() {
        const { todo } = this.props


        return (
            <li className={ todo.completed ? 'completed' : ' ' } >
                <div className="view">
                  <input onChange={this.onToggle} type="checkbox" className="toggle" value="on" checked={todo.completed} />
                  <label><input onChange={this.onToggle} type="checkbox" className="toggle" value="on" checked={todo.completed} />{todo.title}</label>
                  <button onClick={() => ToDoFilter.deleteToDo(todo.id)} className="destroy" />
                </div>
            </li>
        );
    }
}


