import React from 'react';
import todoStore from '../store/ToDoStore'

export default class ToDoEntry extends React.Component {
    state = {
        value: ''
    }

    handleKeyDown(event) {
        if(event.keyCode !== 13) return
        event.preventDefault()
        todoStore.addToDo(this.state.value)
        this.setState({value: ''})
    }

    render() {
        return (
            <header className="header">
                <h1>todo</h1>
                <input value={this.state.value} onKeyDown={event => this.handleKeyDown(event)} onChange={(event) => this.setState({value: event.target.value})} type="text" className="new-todo" placeholder="what needs to be done"></input>
            </header>
        );
    }
}


