import { observable, action } from 'mobx'
import ToDoStore from './ToDoStore'

class ToDoFilter {
    constructor() {
        this.allFilter = this.allFilter.bind(this)
        this.activeFilter = this.activeFilter.bind(this)
        this.comFilter = this.comFilter.bind(this)
        this.deleteAllComp = this.deleteAllComp.bind(this)
        this.updateToShow = this.updateToShow.bind(this)
    }
    @observable toshow = ToDoStore.todos
    @observable flag = "all"

    @action
    allFilter(todos) {
        this.toshow = todos
        this.flag = "all"
    }

    @action
    activeFilter(todos) {
        this.toshow = todos.filter((todo) => todo.completed === false)
        this.flag = "active"
    }

    @action
    comFilter(todos) {
        this.toshow = todos.filter((todo) => todo.completed === true)
        this.flag = "com"
    }

    @action 
    updateToShow() {
        this.toshow = ToDoStore.todos
        if(this.flag === "active") this.activeFilter(ToDoStore.todos)
        if(this.flag === "com") this.comFilter(ToDoStore.todos)
    }

    @action
    deleteToDo(id) {
        let todo = ToDoStore.todos.filter((todo) => todo.id === id)
        let i = ToDoStore.todos.indexOf(todo[0])
        ToDoStore.todos.splice(i , 1)
        this.updateToShow();
    }

    @action
    deleteAllComp() {
        let completedTodos = ToDoStore.todos.filter((todo) => todo.completed === true)
        for(let i = 0 ; i < completedTodos.length ; i++) {
            let x = ToDoStore.todos.indexOf(completedTodos[i])
            ToDoStore.todos.splice(x , 1)
        }
        this.updateToShow();
    }
}

const filter = new ToDoFilter()
export default filter


