import { observable, action } from 'mobx'
import ToDoModel from './ToDoModel';
import ToDoFilter from './ToDoFilter'

class ToDoStore {
    constructor() {
        this.deleteAllComp = this.deleteAllComp.bind(this)
    }
    @observable todos = []
    lastId = 0

    @action
    addToDo(title) {
        this.todos.push(new ToDoModel(this , title , false , this.lastId++))
    }

    @action
    deleteToDo(id) {
        let todo = this.todos.filter((todo) => todo.id === id)
        let i = this.todos.indexOf(todo[0])
        this.todos.splice(i , 1)
        ToDoFilter.toshow = this.todos
    }

    @action
    deleteAllComp() {
        let completedTodos = this.todos.filter((todo) => todo.completed === true)
        for(let i = 0 ; i < completedTodos.length ; i++) {
            let x = this.todos.indexOf(completedTodos[i])
            this.todos.splice(x , 1)
        }
        ToDoFilter.toshow = this.todos
    }
}

const store = new ToDoStore()
export default store


